.product__brand {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
   }

.product__brand div {
    flex: 1;
    max-width: 150px;
    min-width: 120px;
    margin: 2rem;

    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width:450px) {
    .product__brand div {
        min-width: 20px;
        min-height: 10px;
        margin: 3rem;
    }
}